import React from "react";
import styled from "styled-components";

const EnterButton = ({ enterGame }) => {
  return (
    <Button aria-label="게임 입장" onClick={enterGame}>
      ENTER
    </Button>
  );
};

export default React.memo(EnterButton);

const Button = styled.button`
  background: ${({ theme }) => theme.colors.primary.gradient};
  font-size: 2.3rem;
  font-weight: 700;
  width: 30rem;
  height: 6rem;
  margin: 15rem auto 0;
  display: block;
  border-radius: ${({ theme }) => theme.borderRadius.xl};
  border: none;
  color: white;
  cursor: pointer;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  transition: all ${({ theme }) => theme.transitions.normal};
  position: relative;
  overflow: hidden;
  box-shadow: ${({ theme }) => theme.shadows.lg};

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: ${({ theme }) => theme.shadows.xl};
    background: linear-gradient(135deg, rgb(34, 167, 152) 0%, rgb(14, 147, 132) 100%);

    &::before {
      left: 100%;
    }
  }

  &:focus {
    outline: none;
    box-shadow: ${({ theme }) => theme.shadows.xl}, 0 0 0 3px rgba(14, 147, 132, 0.4);
    transform: translateY(-3px);
  }

  &:active {
    transform: translateY(-1px);
    box-shadow: ${({ theme }) => theme.shadows.md};
  }

  @media ${({ theme }) => theme.mobile} {
    font-size: 1.5rem;
    margin: 8rem auto 0;
    height: 5rem;
    width: 20rem;
  }
`;
