import React, { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import A11yTitle from "./A11yTitle";

const Case = React.memo(
  ({ idx, value, gameState, inputCase, resultColor, playerCount, isRevealed, allRevealed }) => {
    return (
      <>
        <A11yTitle element="h3" text="케이스 입력 필드 리스트" />
        <CaseWrapper playerCount={playerCount}>
          <CaseBox resultColor={resultColor} isRevealed={isRevealed}>
            {isRevealed ? value : '?'}
          </CaseBox>
        </CaseWrapper>
      </>
    );
  }
);

const CaseList = ({
  players,
  playerCount,
  gameState,
  results,
  cases,
  checkReady,
  inputCase,
}) => {
  const [allRevealed, setAllRevealed] = useState(false);

  useEffect(() => {
    // 게임이 진행 중이거나 완료된 상태에서는 checkReady를 호출하지 않음
    if (gameState === "playing" || gameState === "done") {
      return;
    }
    // cases가 자동으로 할당되므로 항상 ready 상태로 설정
    if (Object.keys(cases).length && Object.values(cases).every(v => v === 'A' || v === 'B')) {
      checkReady(cases);
    }
  }, [cases, gameState, checkReady]);

  useEffect(() => {
    // 모든 케이스가 도착했는지 확인
    const revealedCount = Object.keys(results).length;
    if (revealedCount === 9 && !allRevealed) {
      setAllRevealed(true);
    }
  }, [results, allRevealed]);

  return (
    <CaseListWrapper allRevealed={allRevealed}>
      {players.map((_, idx) => {
        let result = null;
        for (const player in results) {
          if (results[player] === idx) result = player;
        }
        // 모두 도착하기 전까지는 케이스를 가리고, 모두 도착한 후에만 드러냄
        const isRevealed = allRevealed;

        return (
          <Case
            key={idx}
            idx={idx}
            value={cases[idx]}
            gameState={gameState}
            inputCase={inputCase}
            playerCount={playerCount}
            resultColor={allRevealed && players[result] && players[result].color}
            isRevealed={isRevealed}
            allRevealed={allRevealed}
          />
        );
      })}
    </CaseListWrapper>
  );
};

export default React.memo(CaseList);

const CaseListWrapper = styled.ul`
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  width: 80%;

  @media ${({ theme }) => theme.mobile} {
    width: 100%;
  }
`;

const CaseWrapper = styled.li`
  flex-basis: ${({ playerCount }) => (playerCount < 4 ? "30%" : "20%")};
  padding: 0 0.5%;
  min-width: 0;
`;

const caseStyle = css`
  height: 4rem;
  width: 100%;
  border: 2px solid cornflowerblue;
  border-radius: 5px;
  font-size: 1.6rem;
  text-align: center;

  @media ${({ theme }) => theme.mobile} {
    height: 3rem;
    font-size: 1.4rem;
  }
`;


const CaseBox = styled.span`
  ${caseStyle};
  color: white;
  background-color: ${({ resultColor, isRevealed }) =>
    isRevealed ? (resultColor || "cornflowerblue") : "#6b7280"};
  border: none;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 1rem;
  line-height: 4rem;
  position: relative;
  transition: all ${({ theme }) => theme.transitions.normal};

  ${({ isRevealed }) => !isRevealed && `
    filter: blur(2px);
    opacity: 0.7;
    transform: scale(0.95);
  `}

  ${({ isRevealed }) => isRevealed && `
    animation: revealCase 0.6s ease-out;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 0;
      height: 0;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 50%;
      transform: translate(-50%, -50%);
      animation: ripple 0.6s ease-out;
    }
  `}

  @keyframes revealCase {
    0% {
      opacity: 0;
      transform: scale(0.8) rotateY(90deg);
      filter: blur(4px);
    }
    50% {
      opacity: 0.8;
      transform: scale(1.05) rotateY(0deg);
      filter: blur(1px);
    }
    100% {
      opacity: 1;
      transform: scale(1) rotateY(0deg);
      filter: blur(0px);
    }
  }

  @keyframes ripple {
    0% {
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      width: 200px;
      height: 200px;
      opacity: 0;
    }
  }

  @media ${({ theme }) => theme.mobile} {
    line-height: 3rem;
  }
`;
