const resetCase = (playerCount, forceSameTeam = false, players = []) => {
  const cases = {};
  
  if (forceSameTeam && players.length > 0) {
    // 조셉, 메리, 크리스탈, 캐빈을 같은 팀으로 할당
    const targetNames = ['조셉', '메리', '크리스탈', '캐빈'];
    const targetTeam = Math.random() < 0.5 ? 'A' : 'B';
    const otherTeam = targetTeam === 'A' ? 'B' : 'A';
    
    // 각 플레이어의 인덱스를 찾아서 팀 할당
    const assignments = Array(playerCount).fill(otherTeam);
    
    players.forEach((player, idx) => {
      if (targetNames.includes(player.name)) {
        assignments[idx] = targetTeam;
      }
    });
    
    // 4명이 같은 팀이면 나머지 5명이 다른 팀이므로 자동으로 4:5 비율이 됨
    for (let i = 0; i < playerCount; i++) {
      cases[i] = assignments[i];
    }
  } else {
    // 일반적인 랜덤 할당
    const aCount = Math.random() < 0.5 ? 5 : 4;
    const bCount = playerCount - aCount;
    const assignments = Array(aCount).fill('A').concat(Array(bCount).fill('B'));
    
    // 배열을 섞기
    for (let i = assignments.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [assignments[i], assignments[j]] = [assignments[j], assignments[i]];
    }
    
    for (let i = 0; i < playerCount; i++) {
      cases[i] = assignments[i];
    }
  }
  
  return cases;
};

const getRandomNumber = (min, max) => {
  return Math.floor(Math.random() * (max - min)) + min;
};

const getRandomPlayers = (playerCount, data) => {
  // 모든 플레이어를 포함하되 순서만 랜덤으로 섞기
  const allPlayers = [...data];
  const shuffled = [];
  
  while (allPlayers.length > 0) {
    const randomIndex = Math.floor(Math.random() * allPlayers.length);
    shuffled.push(allPlayers.splice(randomIndex, 1)[0]);
  }
  
  return shuffled;
};

const getRandomLegs = (playerCount) => {
  const legCounts = [];
  const legs = [];
  let rows = new Set();
  let column = 0;

  for (let i = 1; i < playerCount; i++) legCounts.push(getRandomNumber(2, 5));

  while (column < playerCount - 1) {
    if (rows.size === legCounts[column]) {
      legs.push([...rows].sort());
      rows = new Set();
      column++;
    }

    const num = getRandomNumber(0, 9);
    if (column < 1) rows.add(num);
    else {
      const isDuplicate = legs[column - 1].includes(num);
      if (!isDuplicate) rows.add(num);
    }
  }

  return legs;
};

// 이스터에그용 사다리 생성: 특정 플레이어들이 같은 케이스에 도착하도록 조작
const getManipulatedLegs = (playerCount, targetPlayerIndices, targetCaseIndex) => {
  const legs = [];
  const numColumns = playerCount - 1;
  
  // 각 컬럼 초기화
  for (let col = 0; col < numColumns; col++) {
    legs.push([]);
  }
  
  // 사다리를 시뮬레이션해서 타겟 플레이어들이 targetCaseIndex에 도착하도록 역계산
  // 각 타겟 플레이어의 시작 위치에서 targetCaseIndex로 가는 경로 계산
  const simulatePath = (startPos, targetPos, legs) => {
    let currentPos = startPos;
    const path = [];
    
    for (let col = 0; col < numColumns; col++) {
      const canGoRight = currentPos < numColumns && !legs[col].includes(currentPos);
      const canGoLeft = currentPos > 0 && !legs[col].includes(currentPos - 1);
      
      if (currentPos < targetPos && canGoRight) {
        // 오른쪽으로 가야 함
        if (!legs[col].includes(currentPos)) {
          legs[col].push(currentPos);
          legs[col].sort();
          currentPos++;
        }
      } else if (currentPos > targetPos && canGoLeft) {
        // 왼쪽으로 가야 함
        const leftPos = currentPos - 1;
        if (!legs[col].includes(leftPos)) {
          legs[col].push(leftPos);
          legs[col].sort();
          currentPos--;
        }
      }
      // 이미 목표 위치에 있으면 그대로 직진
    }
    
    return currentPos === targetPos;
  };
  
  // 타겟 플레이어들이 모두 targetCaseIndex에 도착하도록 사다리 생성
  // 각 타겟 플레이어에 대해 경로 계산
  targetPlayerIndices.forEach((playerIdx, idx) => {
    simulatePath(playerIdx, targetCaseIndex, legs);
  });
  
  // 나머지 플레이어들을 위한 랜덤 다리 추가 (충돌 방지)
  for (let col = 0; col < numColumns; col++) {
    const existingLegs = new Set(legs[col]);
    const maxLegs = getRandomNumber(2, 5);
    
    while (legs[col].length < maxLegs) {
      const randomRow = getRandomNumber(0, 9);
      // 인접한 다리와 충돌하지 않도록 체크
      if (!existingLegs.has(randomRow) && 
          !existingLegs.has(randomRow - 1) && 
          !existingLegs.has(randomRow + 1)) {
        legs[col].push(randomRow);
        legs[col].sort();
        existingLegs.add(randomRow);
      }
    }
  }
  
  return legs;
};

export { resetCase, getRandomLegs, getRandomPlayers, getManipulatedLegs };
