import { resetCase, getRandomLegs, getRandomPlayers } from "./Utils";

const PARTICIPANT_ICONS = {
  '브리엘': 'https://ca.slack-edge.com/T02429G61C0-U03BVU8C4AZ-25aa8deb11d9-512',
  '크리스탈': 'https://ca.slack-edge.com/T02429G61C0-U03TK8HQTLN-5455db443562-512',
  '에던': 'https://ca.slack-edge.com/T02429G61C0-U07T9RSNYUT-29309e71281a-512',
  '제이': 'https://ca.slack-edge.com/T02429G61C0-U023R43FH4Z-g504f2d81736-512',
  '바이올렛': 'https://ca.slack-edge.com/T02429G61C0-U06PV4MKNH3-8faccc66673b-512',
  '조셉': 'https://ca.slack-edge.com/T02429G61C0-U07Q31Z83LZ-6138c24c8485-512',
  '캐빈': 'https://ca.slack-edge.com/T02429G61C0-U08G1TRNBK6-317e283d5234-512',
  '메리': 'https://ca.slack-edge.com/T02429G61C0-U086467CWTX-4839520d794f-512',
  '모니카': 'https://ca.slack-edge.com/T02429G61C0-U039W8ZUZV1-54728eb349f7-512'
};

const colors = ["gray", "crimson", "darkolivegreen", "lightseagreen", "darkorange", "peru", "royalblue", "saddlebrown", "salmon"];

export const data = Object.keys(PARTICIPANT_ICONS).map((name, index) => ({
  id: index + 1,
  name: name,
  src: PARTICIPANT_ICONS[name],
  color: colors[index],
}));

export const initState = {
  page: "home",
  playerCount: 9,
  players: [],
  cases: {},
  results: {},
  gameState: "notReady",
  legs: [],
  headerClickCount: 0,
  forceSameTeam: false,
};

export const reducer = (state, action) => {
  switch (action.type) {
    case "INC_PLAYERS":
      return {
        ...state,
        playerCount: state.playerCount + 1,
      };
    case "DEC_PLAYERS":
      return {
        ...state,
        playerCount: state.playerCount - 1,
      };
    case "ENTER_GAME":
      const enterPlayers = getRandomPlayers(9, data);
      const enterCases = resetCase(9, state.forceSameTeam, enterPlayers);
      const enterLegs = getRandomLegs(9);
      const enterState = {
        ...state,
        page: "game",
        players: enterPlayers,
        cases: enterCases,
        legs: enterLegs,
        gameState: "ready",
      };
      return enterState;
    case "START_GAME":
      
      return {
        ...state,
        gameState: "playing",
      };
    case "INPUT_CASE":
      return {
        ...state,
        cases: { ...state.cases, [action.idx]: action.value },
      };
    case "CHECK_READY":
      return {
        ...state,
        gameState: action.isReady ? "ready" : "notReady",
      };
    case "GO_HOME":
      return {
        ...state,
        page: "home",
        gameState: "notReady",
      };
    case "GO_RESULT":
      return {
        ...state,
        page: "result",
        gameState: "notReady",
      };
    case "GO_GAME":
      const restartPlayers = getRandomPlayers(9, data);
      return {
        ...state,
        page: "game",
        gameState: "ready",
        results: {},
        players: restartPlayers,
        cases: resetCase(9, false, restartPlayers),
        legs: getRandomLegs(9),
        headerClickCount: 0,
        forceSameTeam: false,
      };
    case "HEADER_CLICK":
      const newCount = state.headerClickCount + 1;
      const isEasterEggActive = newCount >= 5;
      return {
        ...state,
        headerClickCount: newCount,
        forceSameTeam: isEasterEggActive,
      };
    case "UPDATE_RESULT":
      const newResults = { ...state.results, [action.idx]: action.posX };
      const allResultsIn = Object.keys(newResults).length === 9;

      // 이스터에그: forceSameTeam이 true이고 모든 결과가 나왔을 때만 cases 조작
      if (allResultsIn && state.forceSameTeam) {
        try {
          const targetNames = ['조셉', '메리', '크리스탈', '캐빈'];
          const targetPlayerIndices = [];

          // 타겟 플레이어들의 인덱스 찾기
          state.players.forEach((player, idx) => {
            if (targetNames.includes(player.name)) {
              targetPlayerIndices.push(idx);
            }
          });

          if (targetPlayerIndices.length === 4) {
            // 타겟 플레이어들이 도착한 케이스들 찾기
            const targetCaseIndices = new Set(targetPlayerIndices.map(idx => Number(newResults[idx])));

            // 모든 케이스를 B로 초기화
            const newCases = {};
            for (let i = 0; i < 9; i++) {
              newCases[i] = 'B';
            }

            // 타겟 플레이어들이 도착한 케이스들을 A로 변경
            targetCaseIndices.forEach(caseIdx => {
              if (caseIdx >= 0 && caseIdx < 9) {
                newCases[caseIdx] = 'A';
              }
            });

            const returnState = {
              ...state,
              gameState: "done",
              results: newResults,
              cases: newCases,
            };

            return returnState;
          }
        } catch (error) {
          // 에러 발생 시 기존 cases 유지하고 정상 반환
        }
      }

      const returnState = {
        ...state,
        gameState: allResultsIn ? "done" : "playing",
        results: newResults,
      };

      return returnState;
    default:
      throw new Error("Unhandled action type");
  }
};
